.app-wrapper {
    width: 1300px;
    margin: 0 auto;
    display: grid;

    grid-template-areas: "Header Header" "Sidebar Content";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content {
    grid-area: Content;
    background-color: #ffffff;
}




